code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #3c1a1a;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
}

li {
  float: right;
}

li a {
  display: block;
  color: #c98905;
  text-align: center;
  padding: 4px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #111;
}

.active {
  background-color: #691b1c;
}

.break {
  width: 10px;
}

.tableTitle {
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
}
th {
  background-color: #f5f5f5;
}
th, td {
  padding: 20px;
  text-align: -moz-left;
  border-bottom: 1px solid #ddd;
}

tr:hover {background-color:#f5f5f5;}

tbody:hover {background-color:#f5f5f5;}

