.column {
  float: left;
  padding: 20px 8px;
}

.column.left {
  width: 18%;
}

.column.right {
  width: 20%;
}

.column.middle {
  width: 60%;
}

/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column.side, .column.middle {
    width: 100%;
  }
}

